<template>
  <div class="container">
    <div class="header" :style="{backgroundImage: 'url(' + userInfo.userBg + ')'}">
      <div v-if="isLogn">
        <div class="setting">
          <van-icon name="setting-o" @click=" goAccount" />

          <van-uploader :max-size="maxSize" :after-read="uploadFile">
            <p class="flie">点击更换背景</p>
          </van-uploader>
        </div>
        <div class="state">
          <van-row>
            <van-col span="8">
              <!-- <img src="../../assets/images/头.jpg" alt="" class="img_auto"> -->
              <img :src="userInfo.userImg" alt class="img_auto" />
            </van-col>
            <van-col span="10">
              <!-- <router-link to="/login" tag="span"><span class="fl">登录</span></router-link>
              <router-link to="/register" tag="span"><span>注册</span></router-link>-->
              <p class="p">{{userInfo.nickName}}</p>
              <p>简介：{{userInfo.desc == '' ? '这小家伙什么都没留下！' : userInfo.desc}}</p>
            </van-col>
            <van-col span="6"></van-col>
          </van-row>
        </div>
      </div>
      <div v-else class="islogn-false">
        <div class="setting">
          <van-icon name="setting-o" @click=" goAccount" />
          <!-- <van-uploader  :max-size="maxSize" :after-read="uploadFile">
 <p class="flie">点击更换背景</p>
          </van-uploader>-->
        </div>
        <div class="state">
          <van-row>
            <van-col span="8">
              <!-- <img src="../../assets/images/头.jpg" alt="" class="img_auto"> -->
              <img :src="userInfo.img" alt class="img_auto" />
            </van-col>
            <van-col span="10">
              <router-link to="/login" tag="span">
                <span class="fl">登录</span>
              </router-link>
              <router-link to="/register" tag="span">
                <span>注册</span>
              </router-link>
              <!-- <p class="p">{{userInfo.nickName}}</p>
              <p>简介：{{userInfo.desc == '' ? '这小家伙什么都没留下！' : userInfo.desc}}</p>-->
            </van-col>
            <van-col span="6"></van-col>
          </van-row>
        </div>
      </div>
      <div class="content clearfix">
       

        <div class="my">
         
            <van-cell :title="item.text" :icon="item.icon" is-link v-for="(item,index) in tips" :key="index" @click="go(item)"/>
          
        </div>
      </div>

     
    </div>
  </div>
</template>

<script>

export default {
 
   
  
  data() {
    return {
      visible: false,
      tips: [
        { icon: "description", text: "我的订单", name: "订单" },
        { icon: "location-o", text: "我的地址", name: "地址" },
        { icon: "star-o", text: "我的收藏", name: "收藏" },
        { icon: "shopping-cart-o", text: "我的购物车", name: "购物车" },
        { icon: "idcard", text: "安全中心", name: "安全中心" }
      ],
      

      userInfo: "",
      maxSize: 1 * 1024 * 1024,
      isLogn: false
    };
  },
  created() {
     this.$store.state.active = 3;
    if (localStorage.getItem("__jigng")) {
      this.isLogn = true;
    }
    this.findMy();
  },
  methods: {
    //上传图片
    uploadFile(file) {
      let serverBase64Img = file.content.replace(
        /^data:image\/[A-Za-z]+;base64,/,
        ""
      );

      //获取图片类型
      let imgType = file.file.type.split("/")[1];

      let tokenString = localStorage.getItem("__jigng");

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });

      this.axios({
        method: "POST",
        url: "/updateUserBg",
        data: {
          appkey: this.appkey,
          tokenString,
          imgType,
          serverBase64Img
        }
      })
        .then(result => {
          this.$toast.clear();

          if (result.data.code == "I001") {
            
            this.userInfo.userBg = result.data.userBg;
            
          } else {
            this.$toast(result.data.msg);
          }
        })
        .catch(err => {
          this.$toast.clear();
        });
    },
    //跳转
    goAccount() {
      this.$router.push({ name: "账号管理" });
    },
    go(item) {
      if (this.isLogn) {
        this.$router.push({ name: item.name });
      } else {
        this.$toast("请先登录");
      }
    },
    //查询个人信息
    findMy() {
      let tokenString = localStorage.getItem("__jigng");

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });
      this.axios({
        method: "get",
        url: "/findMy",
        params: {
          appkey: this.appkey,
          tokenString
        }
      })
        .then(res => {
          this.$toast.clear();
          if (res.data.code == "A001") {
            this.userInfo = res.data.result[0];
           
          }
          
        })
        .catch(err => {
          
        });
    }
  }
};
</script>

<style lang="less" scoped>
.container {
    position: fixed;
    top: 0;
    bottom: 50px;
    left: 0;
    width: 100%;
    .header {
      .upload-box {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 30px;
        overflow: hidden;
        opacity: 0;
      }
      background-repeat: no-repeat;
      background-size: contain;
      .setting {
        width: 100%;
        // color:white
        color: white;
        .van-icon {
          font-size: 35px;
          float: right;
          margin-top: 25px;
          margin-right: 25px;
        }
        .flie {
          margin-left: 165px;
        }
      }
    }
    .state {
      color: white;
      padding-top: 10px;
      padding-bottom: 30px;
  
      .img_auto {
        width: 70%;
        display: block;
        margin: 0 auto;
        border-radius: 50%;
      }
      .van-row {
        text-align: center;
        line-height: 63px;
        padding: 35px 0;
        span {
          // display: inline-block;
          font-size: 20px;
          color: black;
        }
        .p {
          font-size: 18px;
        }
        p {
          margin: 0;
        }
      }
    }
    .tips {
      background-color: #1bbf80;
      padding-bottom: 15px;
      .van-row {
        text-align: center;
        background-color: #1bbf80;
        p {
          margin: 0;
          padding: 0;
        }
      }
    }
    .content {
      position: fixed;
      top: 200px;
      background-color: white;
      width: 100%;
      border-radius: 20px;
    }
    .order {
      width: 85%;
      margin: 0 auto;
  
      .van-row {
        text-align: center;
        color: #b5b5b5;
        img {
          width: 25%;
          display: block;
          margin: 0 auto;
        }
      }
    }
    .my {
     
        /deep/ .van-cell--clickable {
          cursor: pointer;
          font-weight: 700;
          font-size: 15px;
        }
        /deep/ .van-cell__left-icon {
          margin-right: 15px;
        }
      
    }
  }

  .islogn-false {
    background-color: gray;
    height: 350px;
  }
</style>